.wrapper{
    width: 100%;
    padding:20px;
}
.content{
    width: 100%;
}

.product{
    display:flex;
    flex-direction: column;

}

.body{
    flex-wrap: wrap;
}
.product-item{
    border-radius: 20px;
    background-color: #fff;
    box-shadow: 0 0 8px rgba(0,0,0,0.2);
    display:flex;
    flex-direction: column;
    padding:20px;
    margin-bottom: 20px;
}
.choose{
    margin-left: auto;
    >input{
                transform: scale(1.5);

    }
}
.product-img{
    width: 100%;
    img{
        width: 100%;
        max-height: 400px;
    }
}
.product-name{
    width: 100%;
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.number{
    padding:4px 10px;
    border-radius: 4px;
    background-color: #E8E8E8;
}
.price{
    width: 100%;
    border-radius: 30px;
    background-color: #fff;
    padding:10px 16px;
    display:flex;
    align-items: center;
        box-shadow: 0 0 8px rgba(0,0,0,0.2);
}
.info-place{
    width: 58%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding:0 0px 0 40px;
    .title{
        color:#263646;
    }
    .subtitle{
        color:var(--gray50);
        margin-bottom: 20px;
    }
    .form{
        width: 100%;
    }
   
}
 .form-group{
        width: 100%;
        background-color: #E8E8E8;
        border-radius: 30px;
        padding:8px 16px;
        margin-bottom: 14px;
        >input{
            font-size: 14px;
            background-color: transparent;
            outline: none;
        }
        >textarea{
             font-size: 14px;
            background-color: transparent;
            outline: none;
            min-height: 160px;
            width: 100%;
        }
    }

.pay-item{
    width: 49%;
    padding:6px;
    border-radius: 30px;
    display:flex;
    justify-content: center;
    align-items: center;
    border:1.5px solid #263646;
    >input{
        cursor:pointer;
        transform: scale(1.2);
    }
}
.btn-submit{
    width: 100%;
    padding:8px;
    background-color: #263646;
    color:white;
    font-weight: 400;
    border-radius: 20px;
    margin-top: 20px;
    font-size: 16px;
}
@media screen and (max-width: 639px) {
    .product{
        width: 100%;
    }
    .info-place{
        width: 100%;
        padding:0;
        margin-top: 20px;
    }
}